// vueX
import Home from '@/pages/home/index.vue'
export default {
	components:{
		Home,
	},
	data() {
		return {
			path:'home',
			navList: [{
				title: '主页',
				path: 'home',
				icon: 'icon-dibu-zhuye',
				active: true
			}, {
				title: '商城',
				path: 'shop',
				icon: 'icon-dibu-shangcheng',
				active: false
			}, {
				title: '助手',
				path: 'helper',
				icon: 'icon-dibu-zhushou',
				active: false
			}, {
				title: '我的',
				path: 'my',
				icon: 'icon-wode-touxiangren',
				active: false
			}, ]
		}
	},
	methods: {
		routerTap(item) {
			// 激活切换
			this.navList.forEach(v => {
				if (v == item) {
					v.active = true;
				} else {
					v.active = false;
				}
			})
			// 跳转
			this.path=item.path
		}
	}
}
